body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(90deg, #1CB5E0 0%, #000851 100%);
}
h1 {
  color: white;
  text-transform: uppercase;
  text-align: center;
  margin-top: 2rem;
  font-weight: bold;
}
.container {
  margin: auto;
  padding: 2rem 0;
  max-width: 800px;
  width: 100%;
  min-height: 80vh;
  border-radius: 1rem;
  background-color: white;
}
form {
  margin: 0 auto;
  max-width: 600px;
  text-align: center;
  padding: 0 0 2rem 0;
}
form div label {
  width: 100%;
  font-weight: 600;
  text-align: center;
  font-size: 1.5rem; 
}
form .input {
  height: 40px;
  margin-top: 10px;
}
input[type="submit"]:disabled{
  opacity: .2;
  color: #000;
}
input[type="submit"]{
  border: none;
  color: #fff;
  cursor: pointer;
  font-weight: 600;
  background: #00b0d7;
  text-transform: uppercase;
}
form .input option {
  color: #000;
  background: #ccf4ff;
}
.u-full-width {
  max-width: 550px;
  width: 90%;
}
.mensajes {
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
}
.mensajes h1 {
  color: #454545;
}
.resultado {
  margin: 0 auto;
  padding: 3rem 0;
  border: 2px solid #00b0d7;
}
.resultado p {
  margin: 8px;
}
.error {
  background-color: white;
  border:2px solid red;
  padding: .5rem;
  text-align: center;
  color: red;
  text-transform: uppercase;
  max-width: 600px;
  margin: 0 auto;
}
.montos {
  gap: 10px;
  display: flex;
  align-items: center;
  justify-content: left;
}
.montos .numeros {
  font-weight: 700;
}
.row {
  gap: 30px;
  display: flex;
  flex-direction: column;
}

.spinner {
  width: 40px;
  height: 40px;

  position: relative;
  margin: 100px auto;
}
.double-bounce1, .double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #333;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: sk-bounce 2.0s infinite ease-in-out;
  animation: sk-bounce 2.0s infinite ease-in-out;
}
.double-bounce2 {
  -webkit-animation-delay: -1.0s;
  animation-delay: -1.0s;
}
@-webkit-keyframes sk-bounce {
  0%, 100% { -webkit-transform: scale(0.0) }
  50% { -webkit-transform: scale(1.0) }
}
@keyframes sk-bounce {
  0%, 100% { 
    transform: scale(0.0);
    -webkit-transform: scale(0.0);
  } 50% { 
    transform: scale(1.0);
    -webkit-transform: scale(1.0);
  }
}